import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'

export default {
  name: 'Commodity_brand'
  , data() {
    return {
      tableData: [],
      is_show: 1,
      title: ''
      , title_top: ''
      , add_lis: {}  //
      //
      , base_url: 'uploading'
      , data_url: { type: 2 }
      , data_lis: []
      , fileList: [{ url: '' }]
      , multipleSelection: [] //
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
  }
  , created() {
    var that = this;
    that.get_lis()
    that.base_url = this.axios.defaults.baseURL + that.base_url;
  }
  , methods: {
    get_lis() { //table  
      var that = this;
      http.ajaxPost('brand', { data: { token: http.getToken() } }, res => {
        that.tableData = res.data.data;
      })
    }
    , del_all() { //
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      data.id = id;
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      var http_post = function () {
        http.ajaxPost('brand/destroy', { data: data }, (res) => {
          console.log(res.data)
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
      http.my_alert(that, ',?', '', http_post)
    }
    , handleSelectionChange(val) {  //
      this.multipleSelection = val;
    }
    , all_yes_select(rows) { //   
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    }
    , reverse_select() {
      var that = this;
      if (that.multipleSelection.length == 0) { //  
        that.all_yes_select(that.tableData);
      } else {
        var select_data = []; //
        var new_data = []; //
        for (let i in that.tableData) { //
          new_data.push(that.tableData[i])
        }
        for (var i in that.multipleSelection) { //   
          select_data.push(that.multipleSelection[i])
        }
        that.all_yes_select();//
        //
        for (var i = 0; i < select_data.length; i++) {
          for (var j = 0; j < new_data.length; j++) {
            if (new_data[j] == select_data[i]) {
              new_data.splice(j, 1)
            }
          }
        }
        if (new_data.length == 0) { //
          that.all_yes_select();
        } else {
          that.all_yes_select(new_data);
        }
      }
    }
    , commodity_brand_add() { //  //
      var that = this;
      var data = that.add_lis;
      data.token = http.getToken();
      if (that.add_lis.category_id == null || that.add_lis.name == null || that.add_lis.order == null || that.add_lis.pic == null || that.add_lis.url == null) {
        that.$message({ type: 'info', message: '' })
        return;
      }
      if (that.title_top == '') {
        http.ajaxPost('brand/edit/' + that.add_lis.id, { data: data }, res => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
            that.is_show_updata();
          } else { that.$message({ type: 'info', message: '' }) }
        })
      } else {
        http.ajaxPost('brand/create', { data: data }, res => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
            that.is_show_updata();
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
    }
    , is_show_updata() {  //   
      var that = this;
      that.is_show = !that.is_show;   //
      that.add_lis = {};  //
      that.fileList[0].url = '' //
      that.title = that.is_show ? '' : '';
      that.title_top = that.is_show ? '' : '';
      if (!that.is_show) {
        http.ajaxPost('brand/create', { data: { token: http.getToken() } }, res => {
          that.data_lis = res.data.data
        }
        )
      }
    }
    , commodity_brand_updata(id) { // 
      var that = this;
      that.is_show_updata();
      that.title_top = ''
      http.ajaxPost('brand/edit/' + id, { data: { token: http.getToken() } }, res => {
        that.add_lis = res.data.data.brand
        that.fileList[0].url = that.axios.defaults.baseURL + that.add_lis.pic
      })
    }
    , commodity_brand_del(id) { //  
      var that = this;
      var http_del = function () {
        http.ajaxPost('brand/destroy', { data: { token: http.getToken(), id: [id] } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '' })
            that.get_lis();
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
      http.my_alert(that, ',', '', http_del)
    }
    //
    , pic_status(e) { this.add_lis.pic = e.url; }
  }
}



