<template>
<div>
    <!--  -->
    <div class="Layout_left_home">
        <!-- top -->
        <homelefttop></homelefttop>
        <!--  -->
        <distributionleftcomponents></distributionleftcomponents>
        <!-- bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
     <!--  -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!--  -->
            <div class="search">
                <img class="search_img" src="../../../static/images/search.png" alt="">
                <div class="search_modle" >
                    <span>:</span>
                    <el-input size='small' style="width:208px;display:inlie-block;"  v-model="search.keyword" placeholder=""></el-input>
                    <el-button size='small' @click="search_btn" class="btn" style="margin-left:58px;" type="danger">&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
                </div>
                <div class="search_modle">
                    <span>:{{search.time_data}}</span>
                    <el-date-picker  type="datetimerange" style="width: 398px;height:38px;vertical-align: middle;" v-model="search.times" start-placeholder="" end-placeholder="" :default-time="['12:00:00']"> </el-date-picker>
                </div>
                 <div class="search_modle">
                    <span>:</span>
                    <citycom v-on:province_id="province_id_search" v-on:city_id="city_id_search" v-on:district_id="district_id_search" ></citycom>
                </div>
            </div>
            <!--  -->
            <div class="vip_lis_table">
                <el-table :data="tableData"  @selection-change="handleSelectionChange" ref="multipleTable" border  style="width: 100%">
                    <el-table-column  type="selection"  width="35"> </el-table-column>
                    <el-table-column   label=""  width="320" > 
                        <template   slot-scope="scope" > 
                            <ul class="clearfix">
                                <li style="float:left;width:35%;text-align:center;">
                                    <img :src="scope.row.avater"  width="66" height="66" style="margin-top:28px;display:inline-block;border:1px #eee solid" />
                                </li>
                                <li style="float:left;width:65%;font-size:12px;min-height:58px;">
                                    <p><span style="color:#000">:</span>{{scope.row.name}}  </p>
                                    <p><span style="color:#000">:</span><i v-if="scope.row.father">{{scope.row.father.father_name.name}}</i> </p>
                                </li>
                            </ul>          
                        </template> 
                    </el-table-column>
                    <el-table-column   label=""  >
                        <template   slot-scope="scope" > 
                            <p><span style="display:inline-block;width:64px" >:</span> {{scope.row.balances}}</p>
                            <p><span style="display:inline-block;width:64px" >:</span> {{scope.row.achievement.my_cumulative}}</p>
                        </template> 
                    </el-table-column>
                    <el-table-column   label=""  >
                        <template   slot-scope="scope" > 
                            <p>{{scope.row.created_at | time}}</p>
                        </template> 
                    </el-table-column>
                    <el-table-column   label="" width="60">
                        <template   slot-scope="scope" > 
                        <el-switch @change='status_change(scope.row.status,scope.row.id)' v-model="scope.row.status" :active-value='1' :inactive-value='0' disabled ></el-switch>
                        </template> 
                    </el-table-column>
                    <el-table-column   label="" width="60">
                        <template   slot-scope="scope" > 
                        <el-switch @change='status_change1(scope.row.is_limit,scope.row.id)' v-model="scope.row.is_limit" :active-value='0' :inactive-value='1'  ></el-switch>
                        </template> 
                    </el-table-column>
                    <el-table-column    label=""  width="150">
                        <template slot-scope="scope">
                            <div >
                                <el-button size="mini"  @click="go_vip_details(scope.row.id)" type="primary"></el-button>
                                <el-button size="mini"  @click="vip_other(88,scope.row.id)" type="danger"></el-button>
                            </div>
                            <div style="margin-top:6px">
                                <el-button size="mini" @click="vip_other(1,scope.row.id)" ></el-button> 
                            </div>
                            <div >
                                <el-dropdown style="margin:6px 0">
                                    <el-button type="primary" size="mini"><i class="el-icon-arrow-down el-icon--right"></i>
                                    </el-button>
                                    <el-dropdown-menu slot="dropdown">
                                        <el-dropdown-item @click.native="vip_other(2,scope.row.id)" ></el-dropdown-item>
                                        <el-dropdown-item @click.native="vip_other(4,scope.row.id,scope.row.name)" ></el-dropdown-item>
                                        <el-dropdown-item v-if="scope.row.father" @click.native="vip_other(5,scope.row.id,scope.row.name,scope.row.father.father_name.name)" ></el-dropdown-item>
                                        <el-dropdown-item v-if="!scope.row.father" @click.native="vip_other(5,scope.row.id,scope.row.name,null)" ></el-dropdown-item>
                                        <!-- <el-dropdown-item @click.native="vip_other(10)" ></el-dropdown-item> -->
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </div>
                            
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!--  -->
            <div class="operation" style='padding:12px 0 ;background:#fff'>
                <el-button style="margin-left:28px;" @click="all_yes_select(tableData)"></el-button><el-button @click="all_yes_select()"></el-button>
                <!-- <el-button @click="reverse_select"></el-button><el-button  @click="del_all"></el-button> -->
            </div>
            <!--  -->
            <div class="page"><pagecon :pages="pages" @get_lis="get_lis"></pagecon></div>
        </div>
    </div>
    <div v-if="is_other" class="back_shadow">
        <!--  -->
         <div class="vip_vip_updata" v-if="is_other==1">
            <div class="vip_set_cont_modle_top"><span @click="close" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle">
                <span>:</span><el-input v-model="updata_data.name"  clearable  ></el-input>
                <span>:</span><el-input v-model="updata_data.mobile"  type="number"  clearable ></el-input>
            </div>
             <div class="vip_set_cont_modle">
                <span>QQ:</span><el-input v-model="updata_data.qq"   clearable ></el-input>
                <span>:</span><el-input v-model="updata_data.card"  clearable  ></el-input>
            </div>
            <div class="vip_set_cont_modle">
                <span>:</span><el-input v-model="updata_data.detailed"   clearable ></el-input>
            </div>
            <div class="vip_set_cont_modle">
                <span>:</span>
                <citycom :province_ids='updata_data.province' :city_ids='updata_data.city'  :district_ids='updata_data.district'
                 v-on:province_id="province_ids" v-on:city_id="city_ids" v-on:district_id="district_ids" ></citycom>
            </div>
            <el-button @click="id_updata" type="success" plain></el-button>
        </div>
         <!--  -->
        <div class="vip_vip_updata vip_grade" v-if="is_other==5">
            <div class="vip_set_cont_modle_top"><span @click="close" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle">
                <span>:</span>
                <el-input v-model="updata_user.username"  :disabled="true" ></el-input>
            </div>
            <div class="vip_set_cont_modle">
                <span>:</span>
                <el-input v-model="updata_user.name"  ></el-input>
            </div>
            <el-button type="success" @click="is_updata_user" plain></el-button>
        </div>
        <!--  -->
        <div class="vip_collect" v-if="is_other==2">
            <div class="vip_set_cont_modle_top"><span @click="close" class="el-icon-error"></span></div>
             <div class="vip_set_cont_modle">
                <span>:</span><el-input v-model="receivables.alipay"   placeholder="" clearable></el-input>
            </div>
             <div class="vip_set_cont_modle">
                <span>:</span><el-input  v-model="receivables.alipay_name"  placeholder="" clearable></el-input>
            </div>
             <div class="vip_set_cont_modle">
                <span>:</span><el-input  v-model="receivables.wechat"  placeholder="" clearable></el-input>
            </div>
             <div class="vip_set_cont_modle">
                <span>:</span><el-input  v-model="receivables.bank"  placeholder="" clearable></el-input>
            </div>
             <div class="vip_set_cont_modle">
                <span>:</span><el-input  v-model="receivables.bank_name"  placeholder="" clearable></el-input>
            </div>
             <div class="vip_set_cont_modle">
                <span>:</span><el-input v-model="receivables.bank_type"   placeholder="" clearable></el-input>
            </div>
             <div class="vip_set_cont_modle">
                <span>:</span><el-input v-model="receivables.net_point"   placeholder="" clearable></el-input>
            </div>
            <el-button type="success" @click="updata_receivables" plain></el-button>
        </div>
        <!--  -->
        <div class="vip_vip_updata vip_grade" v-if="is_other==4">
            <div class="vip_set_cont_modle_top"><span @click="close" class="el-icon-error"></span></div>
            <div style="max-height:580px;overflow:auto">
                <div class="vip_set_cont_modle">
                    <span>:</span>
                    <el-input  v-model="recharge.name"  placeholder="" :disabled="true"></el-input>
                </div>
                <div class="vip_set_cont_modle">
                    <span>:</span>
                    <el-select class="select"  v-model="recharge.type" placeholder="">
                        <el-option v-for='(item,index) in recharge_type' :key="index" :label="item" :value="index"  v-if="index!=0" ></el-option>
                    </el-select>
                </div>
                <div class="vip_set_cont_modle">
                    <span>:</span>
                    <el-input  v-model="recharge.number" type="number"  placeholder=""></el-input>
                </div>
                <div class="vip_set_cont_modle">
                    <span>:</span>
                        <el-input  v-model="recharge.remarks" style="width:calc(100% - 200px);vertical-align: middle;" type="textarea" autosize placeholder="" ></el-input>
                </div>
                <el-button type="success" @click="updata_recharge" plain></el-button>
            </div>
        </div>

    </div>
</div>
</template>
<script>
import vip_grade from "./vip_grade";
export default vip_grade;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_img {
      position: absolute;
      width: 40px;
      left: 18px;
      top: calc(50% - 20px);
    }
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 48px;
      line-height: 48px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .vip_lis_table {
    background: #fff;
    margin-top: 18px;
  }
  .operation {
    button {
      padding: 6px 10px;
      vertical-align: middle;
    }
  }
  .page {
    background: #fff;
    border-radius: 12px;
    padding: 18px 0;
    margin-top: 18px;
  }
}
.back_shadow {
  width: 100%;
  min-width: 1000px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4;
  .vip_vip_updata {
    position: absolute;
    width: 768px;
    background: #fff;
    left: calc(50% - 384px);
    top: 15%;
    border-radius: 12px;
    margin-top: 12px;
    padding: 0 0 18px;
    .vip_set_cont_modle_top {
      background: #409eff;
      border-radius: 12px 12px 0 0;
      height: 58px;
      line-height: 58px;
      position: relative;
      text-align: center;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      span {
        position: absolute;
        font-size: 26px;
        color: #ccc;
        float: right;
        top: 16px;
        right: 16px;
        cursor: pointer;
      }
    }
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 108px;
        text-align: right;
        margin-right: 18px;
      }
      i {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
      // 
      .avatar-uploader {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        display: inline-block;
      }
      .avatar-uploader:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
      }
      .avatar {
        width: 178px;
        height: 178px;
        display: block;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
  .vip_grade {
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        width: 128px;
        margin-right: 48px;
      }
      .select {
        width: 258px;
      }
      .el-input {
        width: 260px;
      }
    }
  }
  .vip_collect {
    position: absolute;
    width: 768px;
    background: #fff;
    left: calc(50% - 384px);
    top: 20%;
    border-radius: 12px;
    margin-top: 12px;
    padding: 0 0 18px;
    .vip_set_cont_modle_top {
      background: #409eff;
      border-radius: 12px 12px 0 0;
      height: 58px;
      line-height: 58px;
      position: relative;
      text-align: center;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      span {
        position: absolute;
        font-size: 26px;
        color: #ccc;
        float: right;
        top: 16px;
        right: 16px;
        cursor: pointer;
      }
    }
    .vip_set_cont_modle {
      width: 80%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 128px;
        text-align: right;
        margin-right: 18px;
      }
      .el-input {
        width: 258px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
      // 
      .avatar-uploader {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        display: inline-block;
      }
      .avatar-uploader:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
      }
      .avatar {
        width: 178px;
        height: 178px;
        display: block;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
}
</style>


