import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import pagecon from '@/components/page/pagecon'
import citycom from '@/components/city/city'
import distributionleftcomponents from '@/views/distribution/distribution_left_components.vue'


export default {
    name: 'Tixianshouyi'
    , data() {
      return {
        time_data: null
        , tableData: [] //table
        , pages: null
        , multipleSelection: []       //
        , order: []   //
        , search: {}  //
      }
    }
    , components: {
      homelefttop
      , homeleftbottom
      , pagecon
      , citycom
      ,distributionleftcomponents
    }
    , created() {
      this.get_lis();
    }
    , methods: {
      
      search_btn() {//
        var that = this;
        let data = {};
        data.screen = that.search;
        data.token = http.getToken();
        http.ajaxPost('tixian/get_platform_procedures', { data }, res => {
          if (res.data.status) {
            that.tableData = res.data.data.data;
            that.pages = res.data.data;
            delete that.pages.data;
          } else { that.$message({ type: 'info', message: res.data.message }) ;that.tableData = {} ;}
        })
      }
      ,updata_status(id){
          var that = this ;
          let data = {data:{token:http.getToken(),status:1}} ;
          that.axios.post('tixian/updates/'+ id,data).then(res=>{
              console.log(res.data)
              if(res.data.status){
                that.$message({ type: 'success', message: res.data.message })
              }else{
                that.$message({ type: 'info', message: res.data.message })
                that.get_lis();
              }
          })
          
      }
      , get_lis(e) { //table  
        var that = this;
        http.ajaxPost('tixian/get_platform_procedures', { data: { token: http.getToken(), screen: that.search }, page: e }, res => {
          that.tableData = res.data.data.data;
          that.pages = res.data.data;
          delete that.pages.data;
          console.log(that.tableData)
        })
      }
      , commodity_del(id) { // 
        var that = this;
        var http_post = function () {
          http.ajaxPost('tixian/destroy', { data: { token: http.getToken(), id: id } }, res => {
            if (res.data.status) {
              that.$message({ type: 'success', message: '' })
              that.get_lis();
            } else { that.$message({ type: 'info', message: '' }) }
          })
        }
        http.my_alert(that, ', ', '', http_post);
      }
      ,go_kami_add(){
        this.$router.push({path:'/home/kalman_add'})
      }
      ,kalman_btn(){
        let URL = this.axios.defaults.baseURL + 'kalman/all_export/' + http.getToken();
        window.open(URL, '_blank', 'width=320,height=568,menubar=no,toolbar=no,status=no,scrollbars=yes')
      }
      ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
      , handleSelectionChange(val) {  //
        this.multipleSelection = val;
      }
      , all_yes_select(rows) { //   
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      }
      , reverse_select() {
        var that = this;
        if (that.multipleSelection.length == 0) { //  
          that.all_yes_select(that.tableData);
        } else {
          var select_data = []; //
          var new_data = []; //
          for (let i in that.tableData) { //
            new_data.push(that.tableData[i])
          }
          for (var i in that.multipleSelection) { //   
            select_data.push(that.multipleSelection[i])
          }
          that.all_yes_select();//
          //
          for (var i = 0; i < select_data.length; i++) {
            for (var j = 0; j < new_data.length; j++) {
              if (new_data[j] == select_data[i]) {
                new_data.splice(j, 1)
              }
            }
          }
          if (new_data.length == 0) { //
            that.all_yes_select();
          } else {
            that.all_yes_select(new_data);
          }
        }
      }
      , del_all() { //
        var that = this;
        if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '' }); return; }
        var data = { token: http.getToken() };
        var id = [];
        for (let i in that.multipleSelection) {
          id.push(that.multipleSelection[i].id)
        }
        data.id = id;
        var http_post = function () {
          http.ajaxPost('kalman/destroy', { data: data }, (res) => {
            if (res.data.status) {
              that.get_lis();
              that.$message({ type: 'success', message: '' })
            } else { that.$message({ type: 'info', message: '' }) }
          })
        }
        http.my_alert(that, ',?', '', http_post)
      }
      //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
   
    }
  }


