import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components'
import pagecon from '@/components/page/pagecon'
// import { isNull } from 'util';

export default {
  name: 'Commodity_supplier_lis'
  , data() {
    return {
      value: null,
      tableData: []                 //table
      , multipleSelection: []       //
      , pages: null                 //
      , img_url: ''                 //  
      , go_pc_url: ''
      , go_web_url: ''
      , type_num: '1'               //table   1   0  2   3   4 
      , is_other: 0                 // 
      , other_data: {               //
        name: null,                 //
        rush_price: null            //
        , rush_start_time: null
        , rush_end_time: null
        , integral_price: null      //
        , postage: null             //
        , id: null                  //id
        , market: null              //
        , retail_price: null        //
        , sale_const: null          //
        , supply_cost: null         //
        , integral_deduction: null  //
        , status: null              //
        , auditing: null            //
      }
      , search_data: {              //
        keyword: null               //   
        , search_type: 'name'         //number  name
        , order: null
        , category: null
        , type: null
      }
      , search_get_type: ''         //
      , category: []                //
      , product_type: []            //
      , order_type: []              // 
      , category_all: null          //
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
    , 'pagecon': pagecon

  }
  , created() {
    var that = this;
    that.go_pc_url = that.axios.defaults.baseURL;
    that.go_web_url = http.get_web_url();
    that.img_url = that.axios.defaults.baseURL;
    that.get_lis(1);  //
    that.data_null();
    http.ajaxPost('get_screen', { data: { token: http.getToken(), type: 1 } }, res => {
      that.category.push({ id: 0, name: '' })                                  //
      for (var i = 0; i < res.data.data.category.length; i++) {                       //
        that.category.push({ id: res.data.data.category[i].id, name: res.data.data.category[i].name })
        if (res.data.data.category[i].children) {
          for (var e = 0; e < res.data.data.category[i].children.length; e++) {   //
            that.category.push({ id: res.data.data.category[i].children[e].id, name: '---' + res.data.data.category[i].children[e].name })
            for (var s = 0; s < res.data.data.category[i].children[e].children.length; s++) { //
              that.category.push({ id: res.data.data.category[i].children[e].children[s].id, name: '------' + res.data.data.category[i].children[e].children[s].name })
            }
          }
        }
      }
      that.product_type.push({ name: '', id: 0 })
      that.order_type.push({ name: '', id: 0 })
      for (let i in res.data.data.type) {//
        that.product_type.push({ name: res.data.data.type[i], id: i })
      }
      for (let i in res.data.data.order) {//
        that.order_type.push({ name: res.data.data.order[i], id: i })
      }
    })
  }
  , methods: {
    data_null() { // 
      for (let i in this.other_data) { this.other_data[i] = null }
    }
    , commodity_del(id) { // 
      var that = this;
      var http_post = function () {
        http.ajaxPost('product/destroy', { data: { token: http.getToken(), id: id } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '' })
            that.get_lis();
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
      http.my_alert(that, ', ', '', http_post);
    }
    , get_lis(e) {
      var that = this;
      let data = {};
      if (that.search_get_type == that.type_num) { //
        data = that.search_data;
        data.token = http.getToken();
        data.page = e;
        data.status = that.type_num * 1;
      } else { data = { token: http.getToken(), status: that.type_num * 1, supply: 2 } }
      http.ajaxPost('product', { data: data, page: e }, res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      })
    }
    , search_get() { //
      var that = this;
      that.search_get_type = that.type_num;
      that.get_lis();
    }
    , chang_type_num() { //table 
      this.get_lis();
    }
    , lis_other(type, id, name, price, start, end, supply_cost, integral_deduction, status, auditing) { // 
      var that = this;
      switch (type) {
        case 0: that.$router.push({ path: '/home/commodity_add' }); break;//  
        case 1: const { href } = this.$router.resolve({ path: '/home/commodity_Inventory', query: { id: JSON.stringify(id) } }); window.open(href, '_blank'); break;//  
        case 2: that.$router.push({ path: '/home/', query: { id: id } }); break;//  
        case 3: that.$router.push({ path: '/home/commodity_add', query: { id: id } }); break;//   
        case 4: // 
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.rush_price = price;
          that.other_data.rush_start_time = start * 1000;
          that.other_data.rush_end_time = end * 1000;
          that.other_data.id = id;
          break;
        case 5: // 
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.integral_price = price;
          that.other_data.id = id;
          break;
        case 6: // 
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.postage = price;
          that.other_data.id = id;
          break;
        case 8: // 
          that.is_other = type;
          that.other_data.name = name;
          that.other_data.market = price; //
          that.other_data.retail_price = start; //
          that.other_data.sale_const = end; //
          that.other_data.supply_cost = supply_cost; //
          that.other_data.integral_deduction = integral_deduction; //
          that.other_data.status = status; //
          that.other_data.auditing = auditing; //
          that.other_data.id = id;
          break;
        case 7: // 
          that.is_other = 0;
          that.data_null();
          break;
        default:

      }
    }
    , status_updata(status, id) { // 
      var that = this;
      let data = { token: http.getToken() };
      data.status = status;
      http.ajaxPost('product/edit/' + id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '' })
        } else { that.$message({ type: 'info', message: ',' }); }
      })
    }
    , other_data_updata(type) { //  1  2   3  4  
      var that = this;
      let data = { token: http.getToken() };
      data.name = that.other_data.name;

      if (type == 1) { //
        data.rush_price = that.other_data.rush_price;
        data.rush_start_time = that.other_data.rush_start_time / 1000;
        data.rush_end_time = that.other_data.rush_end_time / 1000;
      } else if (type == 2) { //
        data.integral_price = that.other_data.integral_price;
      } else if (type == 3) { //
        data.postage = that.other_data.postage;
      } else if (type == 4) {
        data.market = that.other_data.market; //
        data.retail_price = that.other_data.retail_price; //
        data.sale_const = that.other_data.sale_const; //
        data.supply_cost = that.other_data.supply_cost; //
        data.integral_deduction = that.other_data.integral_deduction; //
        data.status = that.other_data.status; //
        data.auditing = that.other_data.auditing; //
      }
      that.http_now(data);
    }
    , clear_show(type) { //
      let that = this;
      let data = { token: http.getToken() };
      if (type == 1) { //
        data.rush_end_time = 0;
      } else if (type == 2) { //
        data.integral_price = 0;
      } else if (type == 3) { //
        data.postage = 0;
      }
      that.http_now(data);
    }
    , http_now(data) {  //
      var that = this;
      http.ajaxPost('product/edit/' + that.other_data.id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '' })
          that.get_lis()
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    , all_status() {  // 
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      data.status = that.type_num == '0' ? 1 : 0;
      var http_post = function () {
        http.ajaxPost('product/updates', { data: data }, (res) => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      }
      http.my_alert(that, '?', '', http_post)
    }
    , category_all_updata() { //
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '' }); return; }
      if (that.category_all == null || that.category_all == 0) { that.$message({ type: 'info', message: '' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      data.category_id = that.category_all;
      var http_post = function () {
        http.ajaxPost('product/updates', { data: data }, (res) => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: res.data.message }) }
        })
      }
      http.my_alert(that, '?', '', http_post)
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , handleSelectionChange(val) {  //
      this.multipleSelection = val;
    }
    , all_yes_select(rows) { //   
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    }
    , reverse_select() {
      var that = this;
      if (that.multipleSelection.length == 0) { //  
        that.all_yes_select(that.tableData);
      } else {
        var select_data = []; //
        var new_data = []; //
        for (let i in that.tableData) { //
          new_data.push(that.tableData[i])
        }
        for (var i in that.multipleSelection) { //   
          select_data.push(that.multipleSelection[i])
        }
        that.all_yes_select();//
        //
        for (var i = 0; i < select_data.length; i++) {
          for (var j = 0; j < new_data.length; j++) {
            if (new_data[j] == select_data[i]) {
              new_data.splice(j, 1)
            }
          }
        }
        if (new_data.length == 0) { //
          that.all_yes_select();
        } else {
          that.all_yes_select(new_data);
        }
      }
    }
    , del_all() { //
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      data.id = id;
      var http_post = function () {
        http.ajaxPost('product/destroy', { data: data }, (res) => {
          console.log(res.data)
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
      http.my_alert(that, ',?', '', http_post)
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , proving(name, value) { //
      this[name][value] = this[name][value].replace(/[^\.\d]/g, ''); //
      this[name][value] = this[name][value].match(/^\d*(\.?\d{0,2})/g)[0]; //
    }
  }
  , watch: {
    type_num: function (now, old) {  // 
      this.search_get_type = '';
    }
  }
}


