import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'
import E from 'wangeditor'
export default {
    name: 'Commodity_brand'
    , data() {
        return {
            editorContent: ''                                                       //
            , title: ''
            , img_url: []                                                           // 
            , max_url: { type: 2 }                                                  //  
            , min_url: { type: 1 }                                                  //
            , pic_url: 'uploading'                                                  //
            , all_data: {                                                           //
                category: [{ category_id: null }]                                   //
                , product_parameter: [{ name: null, values: null }]                  //
                , product_plus_price: [{ name: null, values: null }]                 //
                , product_pic: [{ pic_name: null, pic: null, big_pic: null }]       //  
                , number: null                                                      //
                , name: null                                                        //
                , market: 0
                , retail_price: 0
                , sale_const: 0
                , weight: null
                , weight_type: null
                , cover: null
                , pic: null
                , stock: 0
                , sales_volume: 0
                , publisher: null
                , updated_at: new Date()
                , click_volume: null
                , order: null
                , recommend: null
                , hot_sale: null
                , new_product: null
                , status: null
                , content: null
            }
            , brand: []                     //
            , category: []                  //
            , supply: []                    //
            , fileList_pic: []              //
            , fileList_cover: []            //
            , fileList_product_pic_pic: []  //
            , fileList_big_pic: []          //
            , upload_type: {}               //
            , product_level: []             //
            , updata_vip_show: 0
            , cont_is: {
                number: ''
                , name: ''
                , market: ''
                , retail_price: ''
                , sale_const: ''
                , weight: ''
                , weight_type: ''
                , stock: ''
                , sales_volume: ''
                , publisher: ''
                , updated_at: ''
                , click_volume: ''
                , order: ''
                , recommend: ''
                , hot_sale: ''
                , new_product: ''
                , status: '/'
                , content: ''
                , cover: ''
                , pic: ''
            }
            , category_id: null
        }
    }
    , components: {
        'homelefttop': homelefttop
        , 'homeleftbottom': homeleftbottom
        , 'commodityleftcomponents': commodityleftcomponents
    }
    , created() {
        var that = this; //all_data.publisher
        that.pic_url = that.axios.defaults.baseURL + that.pic_url;
        that.get_lis();// Number.isInteger('')  
        if (that.$route.query.id) {  //
            that.title = ''
            http.ajaxPost('product/edit/' + that.$route.query.id, { data: { token: http.getToken() } }, res => {
                if (res.data.status) {
                    that.all_data = res.data.data.product
                    that.all_data.updated_at = that.all_data.updated_at * 1000; //
                    that.editorContent = that.all_data.content;
                    that.all_data.publisher = that.publisher;      //
                    that.fuwenben(); //
                    //------- 
                    that.fileList_cover.push({ name: '', value: that.all_data.cover })// 
                    that.fileList_pic.push({ name: '', value: that.all_data.cover })//
                    // fileList_product_pic_pic  fileList_big_pic  
                    if (that.all_data.product_pic.length == 0) {
                        that.all_data.product_pic = [{ pic_name: null, pic: null, big_pic: null }];
                    } else {
                        for (let i in that.all_data.product_pic) {
                            that.fileList_product_pic_pic.push([{ name: '', value: that.all_data.product_pic[i].pic }]);
                            that.fileList_big_pic.push([{ name: '', value: that.all_data.product_pic[i].big_pic }]);
                        }
                    }
                    //
                    if (that.all_data.product_level) {
                        if (that.all_data.product_level.length > 0) {
                            that.product_level = that.all_data.product_level;
                            that.updata_vip_show = 1;
                        }
                    }
                    //product_parameter   
                    if (that.all_data.product_parameter.length == 0) {
                        that.all_data.product_parameter = [{ name: null, values: null }]
                    } else {
                        for (let i in that.all_data.product_parameter) {
                            delete that.all_data.product_parameter[i].id;
                            delete that.all_data.product_parameter[i].product_id;
                        }
                    }
                    //product_plus_price   
                    if (that.all_data.product_plus_price.length == 0) {
                        that.all_data.product_plus_price = [{ name: null, values: null }]
                    } else {
                        for (let i in that.all_data.product_plus_price) {
                            delete that.all_data.product_plus_price[i].id;
                            delete that.all_data.product_plus_price[i].product_id;
                        }
                    }
                } else { that.$message({ type: 'info', message: ',' }) }
            })
        }
    }
    , mounted() {
        this.fuwenben();
    }
    , updated() {

    }
    , methods: {
        get_lis() {
            var that = this;
            http.ajaxPost('product/create', { data: { token: http.getToken() } }, res => {
                for (var i = 0; i < res.data.data.category.length; i++) {                       //
                    that.category.push({ id: res.data.data.category[i].id, name: res.data.data.category[i].name })
                    if (res.data.data.category[i].children) {
                        for (var e = 0; e < res.data.data.category[i].children.length; e++) {   //
                            that.category.push({ id: res.data.data.category[i].children[e].id, name: '---' + res.data.data.category[i].children[e].name })
                            for (var s = 0; s < res.data.data.category[i].children[e].children.length; s++) { //
                                that.category.push({ id: res.data.data.category[i].children[e].children[s].id, name: '------' + res.data.data.category[i].children[e].children[s].name })
                            }
                        }
                    }
                }
                that.publisher = res.data.data.publisher;               //
                that.all_data.publisher = res.data.data.publisher;      //
            })
        }
        , go_commodity_lis() {   //
            this.$router.push({ path: '/home/commodity_lis' })
        }
        , fuwenben() {          //
            var that = this;
            var editor = new E('#editorElem')
            editor.customConfig.uploadImgShowBase64 = true   //  base64 
            editor.customConfig.onchange = (html) => {
                this.editorContent = html
            }
            editor.customConfig.uploadImgHooks = {
                before: function (xhr, editor, files) { },
                success: function (xhr, editor, result) { },
                fail: function (xhr, editor, result) { },
                error: function (xhr, editor) { },
                timeout: function (xhr, editor) { },
                customInsert: function (insertImg, result, editor) {
                    var url = that.axios.defaults.baseURL + result.url
                    insertImg(url)
                }
            }
            editor.customConfig.uploadImgServer = that.axios.defaults.baseURL + 'uploading'
            editor.customConfig.uploadFileName = 'file'
            editor.customConfig.uploadImgParams = {
                type: 2
            }
            editor.create()
            if (that.$route.query.id) {
                editor.txt.html(that.all_data.content)
            } else {
                editor.txt.html('<p></p>')
            }
        }
        , add_category(index) { // 
            var that = this;
            if (!index) {
                that.all_data.category.push({ category_id: null });
            } else { that.all_data.category.splice(index, 1) }
        }
        , add_product_parameter(index) {//
            var that = this;
            if (!index) {
                that.all_data.product_parameter.push({ name: null, values: null });
            } else { that.all_data.product_parameter.splice(index, 1) }
        }
        , add_product_plus_price(index) {//
            var that = this;
            if (!index) {
                that.all_data.product_plus_price.push({ name: null, values: null });
            } else { that.all_data.product_plus_price.splice(index, 1) }
        }
        , add_product_pic(index) { //
            var that = this;
            if (!index) {
                that.all_data.product_pic.push({ pic_name: null, pic: null, big_pic: null });

            } else { that.all_data.product_pic.splice(index, 1) }
            console.log(that.all_data.product_pic);
        }
        , updata_vip() { //
            var that = this;
            that.all_data.product_level = that.product_level//
            that.updata_vip_show = 1;
        }
        , handleExceed() { //
            this.$message({ type: 'info', message: ',' })
        }
        , beforeRemove() {

        }
        , handleRemove() {

        }
        , handlePreview() {

        }
        , beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif';
            const isLt2M = file.size / 1024 < 500;
            if (!isJPG) {
                this.$message.error(' JPG  png !');
            }
            if (!isLt2M) {
                this.$message.error(' 500kb!');
            }
            return isJPG && isLt2M;
        }

        , proving(name, top, index) { //
            if (index !== undefined) {
                this.all_data[top][index][name] = this.all_data[top][index][name].replace(/[^\.\d]/g, ''); //
                this.all_data[top][index][name] = this.all_data[top][index][name].match(/^\d*(\.?\d{0,2})/g)[0]; //
            } else {
                this.all_data[name] = this.all_data[name].replace(/[^\.\d]/g, ''); //
                if (top === 1) {
                    this.all_data[name] = this.all_data[name].replace('.', ''); //
                } else {
                    this.all_data[name] = this.all_data[name].match(/^\d*(\.?\d{0,2})/g)[0]; // 
                }
            }
            if (name == 'retail_price') {
                this.all_data.distribution = this.all_data.retail_price - this.all_data.sale_const;
            }
            console.log(name)
            if (name == 'sale_const') {
                this.all_data.distribution = this.all_data.retail_price - this.all_data.sale_const;
            }
        }
        , pic_status(e) { // 
            var that = this;
            if (that.upload_type.index === undefined) { //   
                that.all_data[that.upload_type.father] = e.url;
            } else { //  
                that.all_data[that.upload_type.father][that.upload_type.index][that.upload_type.name] = e.url
            }
        }
        , now_add(father, name, index) { //
            this.upload_type = { father: father, name: name, index: index }
        }
        , sure_add() { // 
            var that = this;
            that.all_data.content = that.editorContent; //
            //--------------------------
            for (let i of that.all_data.category) {//
                if (i.category_id === null) {
                    that.$message({ type: 'info', message: '' }); return;
                }
            }
            for (let i in that.all_data) { // 
                if (that.all_data[i] === null || that.all_data[i] === '') {
                    that.$message({ type: 'info', message: ' /  ' + that.cont_is[i] }); return;
                }
            }
            if (that.updata_vip_show) { // ()
                for (let i of that.product_level) {
                    if (i.level_price === null || i.level_price === '') {
                        that.$message({ type: 'info', message: ' ' + i.level_name + '' }); return;
                    }
                }
            }
            for (let i of that.all_data.product_pic) {// 
                if (i.pic_name !== null || i.pic !== null || i.big_pic !== null) {
                    that.$message({ type: 'info', message: '/ /,' }); 
                }
            }
            for (let i of that.all_data.product_parameter) {// 
                if (i.name !== null || i.name === '' || i.value !== null || i.value === '') {
                    that.$message({ type: 'info', message: ',' }); 
                }
            }
            for (let i of that.all_data.product_plus_price) {// 
                if (i.name !== null || i.name === '' || i.value !== null || i.value === '') {
                    that.$message({ type: 'info', message: ',' }); 
                }
            }
            that.all_data.updated_at = that.all_data.updated_at / 1000;
            var data = that.all_data;
            data.token = http.getToken();
            if (that.$route.query.id) {       //
                http.ajaxPost('product/edit/' + that.$route.query.id, { data: data }, res => {
                    if (res.data.status) {
                        that.$message({ type: 'success', message: '' })
                        that.go_commodity_lis();
                    } else { that.$message({ type: 'info', message: res.data.message }); that.all_data.updated_at = that.all_data.updated_at * 1000; }
                })
            } else {                          //
                http.ajaxPost('product/create', { data: data }, res => {
                    if (res.data.status) {
                        that.$message({ type: 'success', message: '' })
                        that.go_commodity_lis();
                    } else { that.$message({ type: 'info', message: res.data.message }); that.all_data.updated_at = that.all_data.updated_at * 1000; }
                })
            }
        }
    }
}



