<template>
<div>
    <!--  -->
    <div class="Layout_left_home">
        <!-- top -->
        <homelefttop></homelefttop>
        <!--  -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!--  -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!--  -->
            <div class="search">
                <div class="search_modle" >
                    <span>{{title}}</span>
                    <el-button type="success" @click="go_commodity_lis"  plain style="vertical-align: middle;width:144px;float:right;margin: 10px;"></el-button>
                </div>
            </div>
            <!--  -->
            <div class="column_modle">
               <ul>
                   <li><span></span><el-input size="mini" style="width:218px" v-model="all_data.number" clearable></el-input></li>
                   <li><span></span><el-input size="mini" style="width:688px"  v-model="all_data.name" clearable></el-input></li>
                   <li><p>PS:ERPERP</p></li>
                   <li v-for="(item,j) in all_data.category" :key="j"><span><i @click="add_category(j)" v-if="j==0">[+]</i><i  @click="add_category(j)" v-if="j!=0">[-]</i></span>
                        <el-select   size='mini' v-model="item.category_id">
                            <el-option  v-for="(items,x) in category" :key="x" :label="items.name"  :value='items.id'> {{items.name}}</el-option>
                        </el-select>
                    </li>
                   <li><p>PS:[+]</p></li>
                   <li>
                        <span></span><el-input @keyup.native="proving('sale_const')" v-model="all_data.sale_const" size="mini" style="width:158px"  type="number"   min="0" step="1" clearable></el-input>
                        <span></span><el-input @keyup.native="proving('retail_price')" v-model="all_data.retail_price" size="mini" style="width:158px" type="number"   min="0" step="1" clearable></el-input>
                    </li>
                  
                    <li><p>PS</p></li>
                    <li v-if="updata_vip_show"><p>PS</p></li>
                    <li>
                        <span></span><el-input  @keyup.native="proving('weight')"  v-model="all_data.weight"  size="mini" style="width:158px" type="number"   min="0" step="1"  clearable></el-input>
                        <span></span><el-radio v-model="all_data.weight_type" :label="1">KG</el-radio><el-radio v-model="all_data.weight_type" :label="0">G</el-radio>
                    </li>
                    <li>
                        <span></span>
                        <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                            :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_cover" :data="max_url" > <el-button @click="now_add('cover','cover')" size="small" type="primary"></el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                        </el-upload>
                        <span style="width:48px"></span>
                        <el-upload style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                             :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_pic" :data="max_url"> <el-button size="small" @click="now_add('pic','pic')" type="primary"></el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                        </el-upload>
                    </li>
                    <li><p>PS280*280450*450</p></li>
                     <!-- <li v-for="(item,index) in all_data.product_pic" :key="index"> -->
                     <li v-for="(item,index) in all_data.product_pic" >
                         <span><i @click="add_product_pic(index)" v-if="index==0">[+]</i><i  @click="add_product_pic(index)" v-if="index!=0">[-]</i></span>
                       <el-input size="mini" style="width:158px" v-model="item.pic_name" clearable></el-input>
                       <span style="width:48px"></span>
                        <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                            :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_product_pic_pic[index]" :data='min_url' ><el-button size="small"  @click="now_add('product_pic','pic',index)" type="primary"></el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                        </el-upload>
                       <span style="width:48px"></span>
                        <el-upload  style="display:inline-block;vertical-align: middle;margin-top:12px;" class="upload-demo" :action="pic_url" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove"
                            :before-upload="beforeAvatarUpload" :on-success='pic_status' :multiple='false'  :limit="1" :on-exceed="handleExceed" :file-list="fileList_big_pic[index]" :data="max_url" ><el-button size="small" @click="now_add('product_pic','big_pic',index)" type="primary"></el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                        </el-upload>
                    </li>
                    <li><p>PS80*80450*450</p></li>
                    <!-- <li v-for="(items,s) in all_data.product_parameter" :key="s" > -->
                    <li v-for="(items,s) in all_data.product_parameter" >
                        <!-- <span><i @click="add_product_parameter(s)" v-if="s==0">[+]</i><i  @click="add_product_parameter(s)" v-if="s!=0">[-]</i></span> -->
                        <span><i></i> </span>
                        <el-input size="mini" style="width:158px" v-model="items.name" clearable></el-input>
                        <span></span>
                        <el-input size="mini"  style="width:358px" v-model="items.values" clearable></el-input>
                    </li>
                    <li ><p>PS X,L,XXL,XXXL ,</p></li>
                    <!-- <li v-for="(items,ss) in all_data.product_plus_price" :key="ss"> -->
                    <li v-for="(items,ss) in all_data.product_plus_price" >
                        <span><i @click="add_product_plus_price(ss)" v-if="ss==0">[+]</i><i  @click="add_product_plus_price(ss)" v-if="ss!=0">[-]</i></span>
                        <el-input size="mini" v-model="items.name" style="width:158px" clearable></el-input>
                        <span></span>
                        <el-input size="mini"  @keyup.native="proving('values','product_plus_price',ss)"   v-model="items.values" style="width:158px" type="number"   min="0" step="1"  clearable></el-input>
                    </li>
                    <li><p>PS</p>
                        <p>88  0 6-12 20</p>
                    </li>
                    <li><span style="font-size:14px;padding:8px 0 ;"></span></li>
                    <li>
                        <div class='tinymce' id="editorElem" style="text-align:left"></div>
                    </li>
                     <li>
                        <span></span><el-input  @keyup.native="proving('stock',1)"  v-model="all_data.stock" size="mini"  style="width:158px" clearable></el-input>
                        <span></span><el-input  @keyup.native="proving('sales_volume',1)"  v-model="all_data.sales_volume" size="mini" style="width:158px" clearable></el-input>
                    </li>
                    <li><p></p></li>
                    <li>
                        <span></span><el-input  v-model="all_data.publisher" size="mini" style="width:158px" clearable :disabled="true"></el-input>
                        <span></span><el-date-picker v-model='all_data.updated_at'   type="datetime" placeholder="" format="yyyy-MM-dd HH:mm:ss"  value-format="timestamp" size='mini' style="width:198px"></el-date-picker>
                        <span></span><el-input  @keyup.native="proving('click_volume',1)"   v-model="all_data.click_volume" size="mini" style="width:158px" type="number"   min="0" step="1"  clearable></el-input>
                    </li>
                    <li>
                        <span></span><el-input  v-model="all_data.order"  size="mini" style="width:158px"  type="number"   min="0" step="1" clearable></el-input>
                    </li>
                    <li><p></p></li>
                     <li>
                        <span></span> <el-radio v-model="all_data.recommend" :label="1"></el-radio><el-radio v-model="all_data.recommend" :label="0"></el-radio>
                    </li>
                     <li>
                        <span></span> <el-radio v-model="all_data.hot_sale" :label="1"></el-radio><el-radio v-model="all_data.hot_sale" :label="0"></el-radio>
                    </li>
                     <li>
                        <span></span> <el-radio v-model="all_data.new_product" :label="1"></el-radio><el-radio v-model="all_data.new_product" :label="0"></el-radio>
                    </li>
                     <li>
                        <span>/</span> <el-radio v-model="all_data.status" :label="1"></el-radio><el-radio v-model="all_data.status" :label="0"></el-radio>
                    </li>
                    <li style="text-align:center"><el-button style="margin-top:28px;" @click="sure_add" type="success">{{title==''?'':''}}</el-button></li>
               </ul>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import commodity_add from "./commodity_add";
export default commodity_add;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    min-height: 100px;
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
    ul {
      width: 96%;
      margin: 0 auto;
      padding: 28px 0;
      li {
        padding: 4px 0;
        span {
          width: 100px;
          display: inline-block;
          text-align: right;
          margin-right: 8px;
          i {
            cursor: pointer;
            font-size: 16px;
            margin-right: 2px;
          }
          i:hover {
            color: red;
          }
        }
        p {
          width: calc(100% - 108px);
          margin-left: 108px;
          color: #ccc;
          padding: 2px 0;
        }
        s {
          color: #ccc;
          text-decoration: none;
          display: inline-block;
          margin-left: 48px;
        }
      }
    }
  }
}
</style>



