<template>
<div>
    <!--  -->
    <div class="Layout_left_home">
        <!-- top -->
        <homelefttop></homelefttop>
        <!--  -->
        <commodityleftcomponents></commodityleftcomponents>
        <!-- bottom -->
        <homeleftbottom></homeleftbottom>
    </div>
    <!--  -->
    <div class="Layout_right_home">
        <div class="Layout_right_box">
            <!--  -->
            <div class="search">
                <div class="search_modle" >
                    <span></span>
                    <el-button type="success" @click="open_add(0)"  plain style="vertical-align: middle;width:144px;float:right;margin: 10px;"></el-button>
                </div>
            </div>
            <!--  -->
            <div class="column_modle">
                <!--   :default-expand-all='true'--> 
                <el-table :data="tableData"   style="width: 100%;" >
                    <el-table-column type="expand" >
                        <template slot-scope="props" style="width:100%;padding:0;margin:0" >
                            <!--  -->
                            <el-table  :show-header='false' :data="props.row.children"  style="width: 100%;color:#0080ff"  >
                                <el-table-column type="expand" style="background:red">
                                    
                                </el-table-column>
                                <el-table-column   label=""  show-overflow-tooltip >
                                    <template   slot-scope="scope" > 
                                    <p>{{scope.row.name}}</p>
                                    </template> 
                                </el-table-column>
                                <el-table-column   label=""  show-overflow-tooltip >
                                    <template   slot-scope="scope" > 
                                    <p>{{scope.row.categoryid}}</p>
                                    </template> 
                                </el-table-column>
                                <el-table-column   label=""   width="90" >
                                    <template   slot-scope="scope" > 
                                        <el-input @change='status_updata("order",scope.row.id,scope.row.order)' type="number" size='mini' v-model.lazy="scope.row.order" ></el-input>                                    
                                    </template> 
                                </el-table-column>
                                <el-table-column   label=""   width="80"  >
                                    <template   slot-scope="scope" > 
                                        <el-switch :active-value= 1  :inactive-value= 0 v-model="scope.row.status" @change='status_updata("status",scope.row.id,scope.row.status)' ></el-switch>                                   
                                    </template> 
                                </el-table-column>
                                <el-table-column   label=""  width="220">
                                    <template slot-scope="scope">
                                        <div >
                                            <el-button size="mini" type="primary">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
                                            <el-button size="mini" type="primary" @click="open_add(scope.row.id)"></el-button>
                                            <el-button size="mini" type="danger" @click="data_del(scope.row.id)" ></el-button> 
                                        </div>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                    </el-table-column>
                    <el-table-column   label=""  show-overflow-tooltip >
                        <template   slot-scope="scope" > 
                        <p>{{scope.row.name}}</p>
                        </template> 
                    </el-table-column>
                    <el-table-column   label=""  show-overflow-tooltip >
                        <template   slot-scope="scope" > 
                        <p>{{scope.row.categoryid}}</p>
                        </template> 
                    </el-table-column>
                    <el-table-column   label=""   width="90" >
                        <template   slot-scope="scope" > 
                            <el-input type="number" @change='status_updata("order",scope.row.id,scope.row.order)' size='mini' v-model.lazy="scope.row.order" ></el-input>                                    
                        </template> 
                    </el-table-column>
                    <el-table-column   label=""   width="80"  >
                        <template   slot-scope="scope" > 
                            <el-switch :active-value= 1  :inactive-value= 0 v-model="scope.row.status" @change='status_updata("status",scope.row.id,scope.row.status)' ></el-switch>                                   
                        </template> 
                    </el-table-column>
                    <el-table-column   label=""  width="220">
                        <template slot-scope="scope">
                            <div >
                                <el-button size="mini" type="primary" @click="open_add(scope.row.id,1)"></el-button>
                                <el-button size="mini" type="primary" @click="open_add(scope.row.id)"></el-button>
                                <el-button size="mini" type="danger"  @click="data_del(scope.row.id)" ></el-button> 
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="commodity_parameter_foot">
                <p></p>
                <p></p>
            </div>
        </div>
    </div>
    <!--  -->
    <div v-if="is_other" class="back_shadow" >
        <div class="vip_vip_updata vip_grade">
            <div class="vip_set_cont_modle_top">{{title_top}}<span @click="close" class="el-icon-error"></span></div>
            <div class="vip_set_cont_modle" style="padding-top:18px"><span>:</span><el-input size='small ' v-model="data_lis.name" style="width:282px"></el-input></div>
            <div class="vip_set_cont_modle">
                <span>:</span>
                <el-select class="select" placeholder="" size='small' v-model="data_lis.category_id" >
                    <el-option v-for="(item,index) in category" :key="index" :label="item.name" :value="item.id">{{item.name}}</el-option>
                </el-select>
                <i style="color:#606222"></i>
            </div>
            <div class="vip_set_cont_modle">
                <span>:</span>
                <el-select class="select" placeholder="" size='small' v-model="data_lis.pid" >
                    <el-option v-for="(item,index) in column" :key="index" :label="item.name" :value="item.id">{{item.name}}</el-option>
                </el-select>
            </div>
             <div class="vip_set_cont_modle">
                <span>:</span>
                <el-radio v-model="data_lis.status" :label="1"></el-radio>
                <el-radio v-model="data_lis.status" :label="0"></el-radio>
            </div>
            <div class="vip_set_cont_modle"><span>:</span><el-input type="number" v-model="data_lis.order" size='small ' ></el-input></div>            
            <el-button type="success" @click="lis_updata" plain> </el-button>
        </div>
    </div>
</div>
</template>
<script>
import commodity_parameter from "./commodity_parameter";
export default commodity_parameter;
</script>
<style lang="scss" scoped>
.Layout_right_box {
  font-size: 12px;
  color: #5c5c5c;
  .search {
    background: #fff;
    border-radius: 12px;
    position: relative;
    .search_modle {
      width: calc(100% - 88px);
      margin-left: 88px;
      height: 58px;
      line-height: 58px;
      span {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        line-height: 48px;
        margin: 0 10px 0 0;
      }
      .select {
        width: 111px;
        display: inlie-block;
        vertical-align: middle;
      }
      .btn {
        margin: 0;
      }
    }
  }
  .column_modle {
    min-height: 100px;
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
  }
  .commodity_parameter_foot {
    background: #fff;
    border-radius: 6px;
    margin-top: 12px;
    height: 58px;
    p:first-child {
      font-weight: 700;
    }
    p {
      width: 90%;
      margin: 0 auto;
      padding-top: 12px;
    }
  }
}
.back_shadow {
  width: 100%;
  min-width: 1000px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4;
  .vip_vip_updata {
    position: absolute;
    width: 768px;
    background: #fff;
    left: calc(50% - 384px);
    top: 15%;
    border-radius: 12px;
    margin-top: 12px;
    padding: 0 0 18px;
    .vip_set_cont_modle_top {
      background: #409eff;
      border-radius: 12px 12px 0 0;
      height: 58px;
      line-height: 58px;
      position: relative;
      text-align: center;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      span {
        position: absolute;
        font-size: 26px;
        color: #ccc;
        float: right;
        top: 16px;
        right: 16px;
        cursor: pointer;
      }
    }
    .vip_set_cont_modle {
      width: 90%;
      margin: 12px auto;
      span {
        font-size: 16px;
        display: inline-block;
        width: 108px;
        text-align: right;
        margin-right: 18px;
      }
      i {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
      }
      .el-input {
        width: 188px;
        display: inlie-block;
        vertical-align: middle;
      }
      .select {
        width: 128px;
      }
    }
    button {
      width: 158px;
      display: block;
      margin: 38px auto 12px;
    }
  }
}
</style>



