import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import vipleftcomponents from '@/views/vip/vip_left_components.vue'
import { watch } from 'fs';
export default {
    name: 'Vip_admin_add'
    , data() {
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.ruleForm.password) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            value: null,
            checked: null,
            check_list: [],
            radio: null //
            , ruleForm: {
                name: null,
                password: null,
                is_password: null,
                mobile: null
            },
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
                ]
                , password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 6, max: 20, message: ' 6  20', trigger: 'blur' }
                ]
                , is_password: [
                    { validator: validatePass2, trigger: 'blur' }
                ]
                , mobile: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 11, max: 11, message: '11', trigger: 'blur' }
                ]
            }
        }
    }
    , components: {
        homelefttop
        , homeleftbottom
        , vipleftcomponents
    }
    , created() {
        var that = this;
    }
    , methods: {
        //
        submitForm(formName) {
            var that = this;

            that.$refs[formName].validate((valid) => {
                    let data = {};
                    data.name = that.ruleForm.name;
                    data.password = that.ruleForm.password;
                    data.mobile = that.ruleForm.mobile;
                    data.token = http.getToken();
                    http.ajaxPost('admin/create', { data }, res => {
                        console.log(res.data)
                        if (res.data.status) {
                            that.$message({ type: 'success', message: '' });
                            that.go_vip_admin_lis();
                        } else { that.$message({ type: 'info', message: res.data.message }) }
                    })
            });
        }
        //
        , go_vip_admin_lis() {
            this.$router.push({ path: '/home/vip_admin_lis' })
        }
 
    }
}



