import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components'
import pagecon from '@/components/page/pagecon'

export default {
  name: 'Commodity_lis'
  , data() {
    return {
      tableData: []                 //
      , pages: {}                   //
      , multipleSelection: []       //
      , keyword: null               //               

    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
    , 'pagecon': pagecon

  }
  , created() {
    var that = this;
    that.get_lis();
  }
  , methods: {
    get_lis(e) {
      var that = this;
      http.ajaxPost('product_comment', { data: { token: http.getToken(), page: e } }, res => {
        that.tableData = res.data.data.data;
        that.pages = res.data.data;
        delete that.pages.data;
      })
    }
    , commodity_del(id) { // 
      var that = this;
      var http_post = function () {
        http.ajaxPost('product_comment/destroy', { data: { token: http.getToken(), id: id } }, res => {
          if (res.data.status) {
            that.$message({ type: 'success', message: '' })
            that.get_lis();
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
      http.my_alert(that, ', ', '', http_post);
    }
    , search_data() {
      var that = this;
      if (that.keyword == null || that.keyword == '') {
        that.$message({ type: 'info', message: '' })
        return;
      }
      http.ajaxPost('product_comment', { data: { token: http.getToken(), keyword: that.keyword } }, res => {
        if (res.data.status) {
          that.tableData = res.data.data.data;
          that.pages = res.data.data;
          delete that.pages.data;
        } else { that.$message({ type: 'info', message: res.data.message }) }
      })
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , handleSelectionChange(val) {  //
      this.multipleSelection = val;
    }
    , all_yes_select(rows) { //   
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    }
    , reverse_select() {
      var that = this;
      if (that.multipleSelection.length == 0) { //  
        that.all_yes_select(that.tableData);
      } else {
        var select_data = []; //
        var new_data = []; //
        for (let i in that.tableData) { //
          new_data.push(that.tableData[i])
        }
        for (var i in that.multipleSelection) { //   
          select_data.push(that.multipleSelection[i])
        }
        that.all_yes_select();//
        //
        for (var i = 0; i < select_data.length; i++) {
          for (var j = 0; j < new_data.length; j++) {
            if (new_data[j] == select_data[i]) {
              new_data.splice(j, 1)
            }
          }
        }
        if (new_data.length == 0) { //
          that.all_yes_select();
        } else {
          that.all_yes_select(new_data);
        }
      }
    }
    , del_all() { //
      var that = this;
      if (that.multipleSelection.length == 0) { that.$message({ type: 'info', message: '' }); return; }
      var data = { token: http.getToken() };
      var id = [];
      data.id = id;
      for (let i in that.multipleSelection) {
        id.push(that.multipleSelection[i].id)
      }
      var http_post = function () {
        http.ajaxPost('product_comment/destroy', { data: data }, (res) => {
          console.log(res.data)
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
      http.my_alert(that, ',?', '', http_post)
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    , go_commodity_evaluate_add() {//
      this.$router.push({ path: '/home/commodity_evaluate_add' })
    }
    , go_lis(id) {
      this.$router.push({ path: '/home/commodity_evaluate_add', query: { id: id } });
    }

  }
}


