import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login_page/login' // 
// ----------------------------------------------------------------------------------------------------
import Home from '@/components/home/home' //
import Home_page from '@/views/home/home_page.vue' //
//   ---------------------------------------------------------------------------------------------
import Vip_lis from '@/views/vip/vip_lis.vue' //vip 
import Vip_setting from '@/views/vip/vip_setting.vue' //vip
import Vip_updata from '@/views/vip/vip_updata.vue' //vip
import Vip_admin_lis from '@/views/vip/vip_admin_lis.vue' //
import Vip_admin_add from '@/views/vip/vip_admin_add.vue' //
import Vip_admin_updata from '@/views/vip/vip_admin_updata.vue' //
import Vip_log_operation from '@/views/vip/vip_log_operation.vue' //
import Vip_vip_add from '@/views/vip/vip_vip_add.vue' //
import Send_letter from '@/views/vip/send_letter.vue' //
import Vip_details from '@/views/vip/vip_details.vue' //
//   ----------------------------------------------------------------------------------------------------

import Commodity_lis from '@/views/commodity/commodity_lis.vue' //
import Commodity_supplier_lis from '@/views/commodity/commodity_supplier_lis.vue' //
import Commodity_column from '@/views/commodity/commodity_column.vue' //
import Commodity_brand from '@/views/commodity/commodity_brand.vue' //
import Commodity_add from '@/views/commodity/commodity_add.vue' //
import Commodity_evaluate_add from '@/views/commodity/commodity_evaluate_add.vue' //
import Commodity_evaluate from '@/views/commodity/commodity_evaluate.vue' //
import Commodity_parameter from '@/views/commodity/commodity_parameter.vue' //
import Commodity_Inventory from '@/views/commodity/commodity_Inventory.vue' //
// ---------------------------------------------------------------------------------------------------------
import Distribution_setting from '@/views/distribution/distribution_setting.vue' //
import Vip_grade from '@/views/distribution/vip_grade.vue' //
import Generation_setting from '@/views/distribution/generation_setting.vue' //
import Updata_product from '@/views/distribution/updata_product.vue' 
import Tixian_setting from '@/views/distribution/tixian_setting.vue' 
import Tixianshouyi from '@/views/distribution/tixianshouyi.vue' 
//
import kalman from '@/views/kalman/kalman.vue' //
import Kalman_add from '@/views/kalman/kalman_add.vue' //
import Kalman_set from '@/views/kalman/kalman_set.vue' //
import Goumai_lis from '@/views/kalman/goumai_lis.vue' //
// -------------------------------------
import Tihuo from '@/views/tihuo/tihuo.vue' //
import Tihuocont from '@/views/tihuo/tihuocont.vue' //
// 
import Huanhuo from '@/views/huanhuo/huanhuo.vue' //
import Zhiding from '@/views/huanhuo/zhiding.vue' 
// 
import Daohang from '@/views/daohang/daohang.vue' //
//  
import Setting from '@/views/setting/setting.vue' //
import Xieyi from '@/views/setting/xieyi.vue' //
import Kefu from '@/views/setting/kefu.vue' //
import Hehuo from '@/views/setting/hehuo.vue' //
Vue.use(Router)
export default new Router({
  mode: 'history',
  // base: __dirname,
  routes: [
    , { path: '/login', name: 'Login', component: Login }
    , {
      path: '/home', name: 'Home', meta: { requireAuth: true }, component: Home
      , children: [
        // --------------------------------------------------------------------------------------------------------------
        { path: 'home_page', name: "Home_page", meta: { requireAuth: true }, component: Home_page }
        //   views--------------------------------------------------------------------------------------------------
        , { path: 'vip_lis', name: "Vip_lis", meta: { requireAuth: true }, component: Vip_lis }
        , { path: 'vip_setting', name: "Vip_setting", meta: { requireAuth: true }, component: Vip_setting }
        , { path: 'vip_updata', name: "Vip_updata", meta: { requireAuth: true }, component: Vip_updata }
        , { path: 'vip_admin_lis', name: "Vip_admin_lis", meta: { requireAuth: true }, component: Vip_admin_lis }
        , { path: 'vip_admin_add', name: "Vip_admin_add", meta: { requireAuth: true }, component: Vip_admin_add }
        , { path: 'vip_admin_updata', name: "Vip_admin_updata", meta: { requireAuth: true }, component: Vip_admin_updata }
        , { path: 'vip_log_operation', name: "Vip_log_operation", meta: { requireAuth: true }, component: Vip_log_operation }
        , { path: 'vip_vip_add', name: "Vip_vip_add", meta: { requireAuth: true }, component: Vip_vip_add }
        , { path: 'send_letter', name: "Send_letter", meta: { requireAuth: true }, component: Send_letter }
        , { path: 'vip_details', name: "Vip_details", meta: { requireAuth: true }, component: Vip_details }
        // ---------------------------------------------------------------------------------------------------------------
        , { path: 'commodity_lis', name: "Commodity_lis", meta: { requireAuth: true }, component: Commodity_lis }
        , { path: 'commodity_supplier_lis', name: "Commodity_supplier_lis", meta: { requireAuth: true }, component: Commodity_supplier_lis }
        , { path: 'commodity_column', name: "Commodity_column", meta: { requireAuth: true }, component: Commodity_column }
        , { path: 'commodity_brand', name: "Commodity_brand", meta: { requireAuth: true }, component: Commodity_brand }
        , { path: 'commodity_add', name: "Commodity_add", meta: { requireAuth: true }, component: Commodity_add }
        , { path: 'commodity_evaluate_add', name: "Commodity_evaluate_add", meta: { requireAuth: true }, component: Commodity_evaluate_add }
        , { path: 'commodity_evaluate', name: "Commodity_evaluate", meta: { requireAuth: true }, component: Commodity_evaluate }
        , { path: 'commodity_parameter', name: "Commodity_parameter", meta: { requireAuth: true }, component: Commodity_parameter }
        , { path: 'commodity_Inventory', name: "Commodity_Inventory", meta: { requireAuth: true }, component: Commodity_Inventory }
        //----------------------------------------------------------------------------------------------------------------
        , { path: 'distribution_setting', name: "Distribution_setting", meta: { requireAuth: true }, component: Distribution_setting }
        , { path: 'vip_grade', name: "Vip_grade", meta: { requireAuth: true }, component: Vip_grade }
        , { path: 'generation_setting', name: "Generation_setting", meta: { requireAuth: true }, component: Generation_setting }
        , { path: 'updata_product', name: "Updata_product", meta: { requireAuth: true }, component: Updata_product }
        , { path: 'tixian_setting', name: "Tixian_setting", meta: { requireAuth: true }, component: Tixian_setting }
        , { path: 'tixianshouyi', name: "Tixianshouyi", meta: { requireAuth: true }, component: Tixianshouyi }
        //
        , { path: 'kalman', name: "Kalman", meta: { requireAuth: true }, component: kalman }
        , { path: 'kalman_add', name: "Kalman_add", meta: { requireAuth: true }, component: Kalman_add }
        , { path: 'kalman_set', name: "Kalman_set", meta: { requireAuth: true }, component: Kalman_set }
        , { path: 'goumai_lis', name: "Goumai_lis", meta: { requireAuth: true }, component: Goumai_lis }
        //
        , { path: 'tihuo', name: "Tihuo", meta: { requireAuth: true }, component: Tihuo }
        , { path: 'tihuocont', name: "Tihuocont", meta: { requireAuth: true }, component: Tihuocont }
        //
        , { path: 'huanhuo', name: "Huanhuo", meta: { requireAuth: true }, component: Huanhuo }
        , { path: 'zhiding', name: "Zhiding", meta: { requireAuth: true }, component: Zhiding }
        //
        , { path: 'daohang', name: "Daohang", meta: { requireAuth: true }, component: Daohang }
        // 
        , { path: 'setting', name: "Setting", meta: { requireAuth: true }, component: Setting }
        , { path: 'xieyi', name: "Xieyi", meta: { requireAuth: true }, component: Xieyi }
        , { path: 'kefu', name: "Kefu", meta: { requireAuth: true }, component: Kefu }
        , { path: 'hehuo', name: "Hehuo", meta: { requireAuth: true }, component: Hehuo }
      ]
      , redirect: '/home/home_page'
    }
    , { path: '**', redirect: '/home/home_page' }
  ]
})



