import http from '@/api/http'
import homelefttop from '@/components/home_left/home_left_top'
import homeleftbottom from '@/components/home_left/home_left_bottom'
import commodityleftcomponents from '@/views/commodity/commodity_left_components.vue'
export default {
  name: 'Commodity_parameter'
  , data() {
    return {
      tableData: [],
      is_other: 0
      , category: [{ id: 0, name: '' }] //
      , column: [{ id: 0, name: '' }] //
      , title_top: ''
      , data_lis: {}  //
    }
  }
  , components: {
    'homelefttop': homelefttop
    , 'homeleftbottom': homeleftbottom
    , 'commodityleftcomponents': commodityleftcomponents
  }
  , created() {
    var that = this;
    that.get_lis()
  }
  , methods: {
    open_add(id, e) {
      var that = this;
      that.is_other = 1;  //
      that.title_top = id ? '' : ''
      if (id && e == null) { //
        http.ajaxPost('column/edit/' + id, { data: { token: http.getToken() } }, res => {
          that.data_lis = res.data.data.column;
        })
      }
      if (id && e != null) {  //
        that.title_top = ''
        that.data_lis.pid = id;
      }
    }
    , close() {
      this.is_other = 0;
      this.data_lis = {};
    }
    , get_lis() {
      var that = this;
      http.ajaxPost('column', { data: { token: http.getToken() } }, res => {
        that.tableData = res.data.data
      })
      http.ajaxPost('column/create', { data: { token: http.getToken() } }, res => {
        for (let i of res.data.data.category) {
          that.category.push(i)
        }
        for (let i of res.data.data.column) {
          that.column.push(i)
        }
      })
    }
    , lis_updata() { //
      var that = this;
      var data = that.data_lis;
      data.token = http.getToken();
      if (that.title_top == '') {  //
        if (that.data_lis.name == null || that.data_lis.category_id == null || that.data_lis.pid == null || that.data_lis.status == null || that.data_lis.order == null) {
          that.$message({ type: 'info', message: '' })
          return;
        }
        http.ajaxPost('column/create', { data: data }, res => {
          if (res.data.status) {
            that.get_lis();
            that.close()
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: '' }) }
        })
      } else { // 
        http.ajaxPost('column/edit/' + that.data_lis.id, { data: data }, res => {
          if (res.data.status) {
            that.get_lis();
            that.close()
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
    }
    , data_del(id) {  //
      var that = this;
      var del_http = function () {
        http.ajaxPost('column/destroy', { data: { token: http.getToken(), id: [id] } }, res => {
          if (res.data.status) {
            that.get_lis();
            that.$message({ type: 'success', message: '' })
          } else { that.$message({ type: 'info', message: '' }) }
        })
      }
      http.my_alert(that, ', ', '', del_http)
    }
    , status_updata(name, id, value) { //status 
      var that = this;
      var data = { token: http.getToken() }
      data[name] = value;
      console.log(value)
      http.ajaxPost('column/edit/' + id, { data: data }, res => {
        if (res.data.status) {
          that.$message({ type: 'success', message: '' })
        } else { that.$message({ type: 'info', message: '' }) }
      })
    }
  }
}



